import { record } from 'rrweb'
import axios from 'axios'


const rec_axios = axios.create(null)

const randStr = len => {
  let str = ''
  while (str.length < len) {
    str += Math.random().toString(36).substr(2)
  }
  return str.substr(0, len)
}

const setStore = (key, val, expires) => {
  val = typeof val === 'object' ? JSON.stringify(val) : val
  if (typeof expires === 'number') {
    expires = expires + Math.trunc(Date.now() / 1000)
    val = val + '|' + expires
  }
  localStorage.setItem(key, val)
}

const getStore = (key) => {
  let item = localStorage.getItem(key)
  if (item) {
    let now = Math.trunc(Date.now() / 1000)
    let mth = item.match(/\|\d{10}$/)
    let time = mth ? mth[0].slice(1) : Infinity
    if (time - now > 0) {
      item = item.replace(/\|\d+$/, '')
      let itx = ''
      try {
        itx = JSON.parse(item)
      } catch (e) {}
      return typeof itx === 'object' ? itx : item
    }
  }
  localStorage.removeItem(key)
  return null
}

const G = {
  recId: getStore('RR_REC[recId]') || `r_${randStr(12)}`,
  appid: '',
  recArr: [],  // 录制的数组
  recLSArr: [],  // 本地存储录制数组
  count: +localStorage.getItem('RR_REC[count]') || 0,
  errCount: 0,  // 错误次数
  isScrolling: false,  // 是否在滚动(滑动)滚动条
  isReqLogServer: true,  // 是否是专属服务器
  total: 0,  //录制总条数
  blockList: [],  // 屏蔽dom名称
  timer1t: 0,
}

const R = {
  initREC() {
    window.onload = () => {
      let rid = getStore('RR_REC[recId]')
      let appid = localStorage.getItem('RR_REC[appid]')
      if (appid && rid) {
        setTimeout(() => {
          R.start(appid)
        }, 3000)
      }
    }
    
    window.addEventListener('scroll', e => {
      G.isScrolling = true
      clearTimeout(G.timer1t)
      G.timer1t = setTimeout(() => {
        G.isScrolling = false
      }, 300)
    })
  },
  
  start(appid = '') {
    // todoz
    // if (!localStorage.getItem('allowRec')) return
    
    
    // todoz
    if (!(/(iPhone|iPod|iPad|Android|ios)/i.test(navigator.userAgent))) {
      return
    }
    
    if (!appid) {
      throw new Error('请传入appid')
    }
    G.appid = appid
    localStorage.setItem('RR_REC[appid]', appid)
    document.getElementById('rec_id').innerText = G.recId
    setStore('RR_REC[recId]', G.recId, 1800)
    // todoz
    localStorage.removeItem('rrweb-events')
    R.clearSucLS()
    
    let last = 0
    
    const stopFn = record({
      emit(event) {
        if (R.isRecSuc()) {
          return stopFn()
        }
        let now = Date.now()
        
        if (!R.isRecSuc() && !document.hidden) {
          // if (now - last < 1) {
          //   return
          // }
          if (!G.isScrolling || now - last > 50) {
            last = now
            event.count = G.count
            G.recArr.push(event)
            // todoz.
            if (R.isSaveRec()) {
              G.recLSArr.push(event)
            }
            G.count++
            localStorage.setItem('RR_REC[count]', G.count)
          }
        }
        
      },
    })
    
    if (R.isRecSuc()) return
    
    R.reqRecDataZ()
    R.reqRecErrDataZ()
    R.resetReqUrl()
  
    setTimeout(R.judgeReqDom, 3000)
  },
  
  end() {
    localStorage.setItem('RR_REC[recSuc]', 'true')
    G.recArr = []
    G.count = 0
    let arr = ['count', 'appid', 'recId', 'isReqDomSuc']
    arr.forEach(v => {
      localStorage.removeItem(`RR_REC[${v}]`)
    })
    
  },
  
  isRecSuc() {
    return localStorage.getItem('RR_REC[recSuc]') === 'true'
  },
  
  eachAndSetBlock() {
    let arr = G.blockList
    if (arr.length) {
      document.querySelectorAll(arr.join(',')).forEach(v => {
        v.classList.add('rr-block')
      })
    }
  },
  
  // todoz
  saveLSTimer_tmp() {
    // const arr = JSON.parse(localStorage.getItem('rrweb-events') || '[]')
    // if (!R.isRecSuc() && G.recLSArr.length) {
    //   const res = [...arr, ...G.recLSArr]
    //   G.recLSArr = []
    //   localStorage.setItem('rrweb-events', JSON.stringify(res))
    // }
    setTimeout(R.saveLSTimer_tmp, 3000)
  },
  
  // 判断是否提交type!=3的数据(前两个)
  async judgeReqDom() {
    if (R.isDomReqSuc()) {
      G.recArr = G.recArr.filter(v => v.type === 3)
      return
    }
    let data = G.recArr.filter(v => v.type === 2 || v.type === 4)
    try {
      const res = await R.reqRecDataPost(data)
      if (res.status === 200) {
        G.errCount = 0
        G.recArr = G.recArr.filter(v => v.type === 3)
        localStorage.setItem('RR_REC[isReqDomSuc]', 'true')
        return
      }
    } catch(e) {
      G.errCount++
      setTimeout(R.judgeReqDom, 10e3)
    }
  },
  
  isDomReqSuc() {
    return !!localStorage.getItem('RR_REC[isReqDomSuc]') || R.isRecSuc()
  },
  
  // 提交成功后删除提交的数据
  removeSucArr(list) {
    G.recArr = G.recArr.filter(v => {
      return !list.some(c => c.count === v.count)
    })
  },
  
  // 失败数据提交成功后删除提交的数据
  removeSucErrArr(list) {
    const res = R.getErrArr().filter(v => {
      return !list.some(c => c.count === v.count)
    })
    localStorage.setItem('RR_REC[errArr]', JSON.stringify(res))
  },
  
  getErrArr() {
    return JSON.parse(localStorage.getItem('RR_REC[errArr]') || '[]')
  },
  
  // 定时提交数据
  async reqRecDataZ() {
    const arr = G.recArr.filter(v => v.type === 3).slice(-200)
    const larger = arr.filter(v => JSON.stringify(v).length > 1e5)
    const data = arr.filter(v => JSON.stringify(v).length <= 1e5)
    larger.length && R.pushErrArrToLS(larger)
    
    try {
      if (data.length) {
        if (G.errCount > 2) {
          G.isReqLogServer = !G.isReqLogServer
        }
        const res = await R.reqRecDataGet(data)
        if (res.status === 200) {
          G.errCount = 0
          R.removeSucArr(arr)
        }
      }
    } catch (e) {
      G.errCount++
      R.pushErrArrToLS(data)
    }
    clearTimeout(window.tm2s)
    window.tm2s = setTimeout(R.reqRecDataZ, 10e3)
  },
  
  // 定时提交请求失败的数据
  async reqRecErrDataZ() {
    // 防止页面卡顿, 最多只提交200条数据
    const sArr = R.getErrArr().slice(-200)
    if (sArr.length) {
      try {
        const res = await R.reqRecDataPost(sArr)
        if (res.status === 200) {
          G.errCount = 0
          R.removeSucErrArr(sArr)
        }
      } catch(e) {
        G.errCount++
      }
    }
    clearTimeout(window.tm3s)
    window.tm3s = setTimeout(R.reqRecErrDataZ, 10e3)
  },
  
  // 定时把提交的服务器重置为专属日志服务器
  resetReqUrl() {
    clearTimeout(window.tm1s)
    window.tm1s = setTimeout(() => {
      R.resetReqUrl()
      G.isReqLogServer = true
    }, 5000)
  },
  
  reqRecDataPost(list = []) {
    let data = {
      rec_id: G.recId,
      appid: G.appid,
      policy_no: localStorage.getItem('RR_REC[policy_no]') || '',
      quote_no: localStorage.getItem('RR_REC[quote_no]') || '',
      list,
    }
    return rec_axios({
      url: G.isReqLogServer ? '//dig.anlaa.com/digpost.php' : '//empty_test.anlaa.com/digpost.php',
      method: 'POST',
      data,
    })
  },
  
  reqRecDataGet(list = []) {
    let data = {
      rec_id: G.recId,
      appid: G.appid,
      policy_no: localStorage.getItem('RR_REC[policy_no]') || '',
      quote_no: localStorage.getItem('RR_REC[quote_no]') || '',
      total: G.total,
      list,
    }
    return rec_axios({
      url: G.isReqLogServer ? '//dig.anlaa.com' : '//empty_test.anlaa.com',
      method: 'GET',
      params: {
        data,
      },
    })
  },
  
  // 设置保单号
  setPolicyNum(num = '') {
    localStorage.setItem('RR_REC[policy_no]', num)
    G.total = G.count
    R.reqRecDataZ()
    R.end()
  },
  
  // 设置报价号
  setQuoteNum(num = '') {
    localStorage.setItem('RR_REC[quote_no]', num)
    R.reqRecDataZ()
  },
  
  clearSucLS() {
    let arr = ['recSuc', 'policy_no', 'quote_no']
    arr.forEach(v => {
      localStorage.removeItem(`RR_REC[${v}]`)
    })
  },
  
  pushErrArrToLS(data) {
    const res = [...this.getErrArr(), ...data]
    localStorage.setItem('RR_REC[errArr]', JSON.stringify(res))
  },
  
  isSaveRec() {
    return !!localStorage.getItem('isSaveRec')
  },

}

R.initREC()

if (R.isSaveRec()) {
  R.saveLSTimer_tmp()
}

export default {
  install(Vue, opt = {}) {
    Vue.prototype.$R_REC = R
  }
}