<script>
export default {
  name: 'App',
  mounted() {
    window.addEventListener('keydown', e => {
      if (e.altKey && e.key === 'r') {
        this.$R_REC.start('1db3b31ceb888ec3c221c491469e8ba1')
      }
      if (e.altKey && e.key === 's') {
        this.$R_REC.setPolicyNum()
      }
    })
  },
}
</script>


